import React, { useState } from 'react';
const question = [
  {
    title: 'Qu productos puedo encontrar en HENRYTECH?',
    description: `  Con nosotros puedes encontrar todo tipo de productos relacionados con
  la tecnologa, estamos a la vanguardia con los mejores productos y de
  la mejor calidad.`,
  },
  {
    title: 'Qu productos puedo encontrar en HENRYTECH?',
    description: `Estamos ubicados en: ...`,
  },
  {
    title: 'Es seguro comprar en esta tienda?',
    description: ` Claro! Con nosotros https://doctormovil.co/ puedes estar tranquilo y
  hacerlo con toda la confianza. En nuestro sitio web tomamos todas las
  medidas de seguridad para proteger el proceso de pago y tu informacin`,
  },
  {
    title: 'Cules son los pagos?',
    description: ` Nuestro sitio web recibe los metodos de pago: Tarjetas de crdito
  (Visa, American Express, Mastercard, Diners Club). Tarjetas dbito por
  PSE, Nequi o Daviplata`,
  },
  {
    title: 'Hacen envos nacionales?',
    description: `Claro que s, tenemos cobertura en toda Colombia.`,
  },
  {
    title: 'Cul es el costo del envo?',
    description: `Nuestros costos de envo varan segn tu ubicacin, este ser
  calculado al momento del Check out`,
  },
  {
    title: 'Cmo gestionar la garanta de mi producto?',
    description: ` Debes enviar el producto a nuestra tienda o dirigirte personalmente
  explicando el problema del producto, y en las condiciones en que se
  presenta, ejemplo: mi equipo se recalienta y se apaga cuando lo pongo
  a cargar.`,
  },
];
const Questions = () => {
  const [open, setOpen] = useState(null);
  const handleClick = (index) => {
    if (open === index) {
      return setOpen(null);
    }
    setOpen(index);
  };
  return (
    <div className='questions'>
      <h1 className='questions__h1'>Preguntas frecuentes HENRYTECH</h1>
      <p className='questions__text'>
        Hola! Sabemos que es posible que surjan algunas dudas antes de hacer
        una compra y queremos que ests muy seguro de que el paso que vas a
        tomar es totalmente satisfactorio. Por esta razn creamos esta seccin
        en donde encontrars las preguntas ms frecuentes y as poderte
        garantizar que la inversin que hars con nosotros sea tu mejor
        decisin.
      </p>
      <div className='questions__div'>
        {question.map((quest, index) => (
          <>
            <h2 onClick={() => handleClick(index)} className='questions__h2'>
              {quest.title}
            </h2>
            <p className={`questions__p ${open === index ? 'v' : ''}`}>
              {quest.description}{' '}
            </p>
          </>
        ))}
      </div>
    </div>
  );
};

export default Questions;
